import React, { Component } from 'react';
import { BrowserRouter as Router, Route} from 'react-router-dom';
import Header from './components/layout/Header';
import Todos from './components/Todos';
import AddTodo from './components/AddTodo';
import About from './components/pages/About';
import axios from 'axios';
//import uuid from 'uuid';  // install to create ids
import './App.css';
// node.js installed to use npm to create-react-app (see github)
class App extends Component {
 
    state = {
      todos: [
       /* {
          id: uuid.v4(),
          title: 'Take out trash',
          completed: false
        },
        {
          id: uuid.v4(),
          title: 'Take wife to dinner',
          completed: false
        },
        {
          id: uuid.v4(),
          title: 'read war and peace',
          completed: false
        }*/
        ]
  }
  
  componentDidMount() {
    axios.get('https://jsonplaceholder.typicode.com/todos?_limit=10')
    .then(res => this.setState({todos:res.data}));
  }
  
  markComplete = (id) => {
  //  console.log(id);
    this.setState({ todos: this.state.todos.map(todo => {
    if(todo.id === id) {
      //toggle
      todo.completed = !todo.completed;
    }
    return todo;
  }) });
  }
  
  delTodo = (id) => {
   axios.delete(`https://jsonplaceholder.typicode.com/todos/${id}`)
   .then(res => this.setState({todos : [...this.state.todos.filter(todo => todo.id !== id)] }));
  }
  
  addTodo = (title) => {
    /*const newTodo ={
      id: uuid.v4(),
      title: title,
      completed: false
    };*/
    axios.post('https://jsonplaceholder.typicode.com/todos', 
    { title: title,
      completed: false
    })
    .then(res =>  this.setState({todos : 
    [...this.state.todos, res.data] }));
  }
  
  render() { //this JSX
    return ( 
      <Router>
        <div className="App">
          <div className = "container">
            <Header />
            <Route exact path="/" render={props => (
              <React.Fragment>
                <AddTodo addTodo={this.addTodo} />
                <Todos /*pass STATE FROM ABOVE TO todos.js*/ todos={this.state.todos} 
                markComplete = {this.markComplete} delTodo ={this.delTodo}/>
              </React.Fragment>
            )} />
            <Route path="/about" component={About} />
          </div>
        </div>
      </Router>
      
      );
    }
  }
  
  export default App;

